import { getIn } from 'final-form';
import { useMemo, useState, useEffect } from 'react';
import { useForm } from 'react-final-form';
import { useBlurredField } from '../../shared/index.js';

const createValueMatrix = (dataElements, sortedCOCs, formState) => dataElements.map(de => sortedCOCs.map(coc => getIn(formState.values, "".concat(de.id, ".").concat(coc.id))));
/**
 * Generates matrix (2d-array) of the values in a table, defined by dataElements and
 * the categoryOptionCombos.
 * @param {*} dataElements dataElements in order as rendered in table, these are "rows"
 * @param {*} sortedCOCs categoryOptionCombos in order as rendered in table, these are the "columns"
 */


export const useValueMatrix = function () {
  let dataElements = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : [];
  let sortedCOCs = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : [];
  const form = useForm();
  const blurredField = useBlurredField();
  const [valueMatrix, setValueMatrix] = useState(createValueMatrix(dataElements, sortedCOCs, form.getState()));
  const affectedFieldsLookup = useMemo(() => new Set(dataElements.flatMap(de => sortedCOCs.map(coc => "".concat(de.id, ".").concat(coc.id)))), [dataElements, sortedCOCs]);
  const containsAffectedField = useMemo(() => affectedFieldsLookup.has(blurredField), [blurredField, affectedFieldsLookup]);
  useEffect(() => {
    if (containsAffectedField) {
      setValueMatrix(createValueMatrix(dataElements, sortedCOCs, form.getState()));
    }
  }, [containsAffectedField, dataElements, form, sortedCOCs]);
  return valueMatrix;
};